// eslint-disable-next-line import/no-unresolved
import CardsApi from '@/api/titleCard';
import MainPage from '../main/index';
import SingerDetail from '../singerInfo/index';
import Tabs from '@/components/common-bottom-tabs';
import TabsList from '@/data/tabs';
import Card from '@/components/article';
// import { Loading } from 'element-ui';


export default {
  name: 'sasa',
  components: {
    Tabs,
    Card,
    SingerDetail,
    MainPage,
  },
  data() {
    return {
      tab: 0,
      isFocus: false,
      loading: false,
      list1: [],
      list: [
      ],
    };
  },
  mounted() {
    this.feacthPage();
    this.getCards();
  },
  methods: {
    feacthPage() {
      this.list = TabsList; // tabs list
    },
    getCards() {
      CardsApi.CardsApi().then((res) => {
        console.log(res);
        this.list1 = res.arr;
      });
    },
    goNext() {
      this.loading = true;
      this.getCards();
      setTimeout(() => {
        this.loading = false;
      }, 2000);
    },
    tabEmit(e) {
      console.log(e);
      this.tab = parseInt(e);
    },
    inputFocus() {
      this.isFocus = true;
    },
    inputBlur() {
      this.isFocus = false;
    },
  },
};



